@import url('https://fonts.googleapis.com/css2?family=Biryani:wght@200;700&display=swap');

* {
    margin: 0;
    padding: 0;
    text-decoration: none;
    list-style: none;
    font-family: 'Biryani', sans-serif;
}

body {
    background-color: rgb(41, 41, 41);
    color:white;
    text-shadow: 1px 1px 5px gray;
}

h1 {
    margin-top: 25px;
    margin-bottom: 25px;
}

.centered {
    text-align: center;
}

button {
    background-color: rgb(196, 243, 255);
    border: 1px solid rgba(128, 128, 128, 0.753);
    border-radius: 4px;
}

button:hover {
    box-shadow: 1px 1px 5px gray;
}

.link-react {
    color: white; text-decoration: underline;
}

textarea {
    resize: none;
}

nav {
    display: flex;
    align-items: center;
    padding: 25px;
    background-color: darkcyan;
}

nav h1 {
    margin: 0;
}

.logo {
    color: white;
}

nav a {
    color:aquamarine;
    text-shadow: 1px 1px 3px gray;
    margin-left: 25px;
}

@media screen and (max-width: 540px)
{
    nav {
        padding: 10px;
    }

    .logo {
        margin-left: 0;
        display: inline-block;
    }

    nav a {
        margin-left: 10px;
    }

    .nav-links {
        display: grid;
        grid-auto-flow: column;
        margin-left: 5px;
    }
}

footer {
    position: absolute;
    bottom: 0;
    text-align: center;
    width: 100%;
    background-color: darkcyan;
    padding-top: 15px;
    padding-bottom: 15px;
    box-shadow: 0px -1px 3px gray;
}

.heading {
    text-align: center;
}

.heading-body {
    margin-top: 0.25px;
    text-align: center;
    padding-left: 0;
    padding-right: 0;
    padding-top: 25px;
    padding-bottom: 25px;
    border-top: 1px solid gray;
    border-bottom: 1px solid gray;
    /* background-color: whitesmoke; */
}

.App {
    padding-bottom: 50px;
    position: relative;
    min-height: 100vh;
}

/* section for my gaming apps/web apps */
.profiles {
    padding-left: 0;
    padding-right: 0;
    padding-top: 25px;
    padding-bottom: 25px;
}

.scroll-list {
    width: 100%;
    display: grid;
    grid-auto-flow: column;
    place-items: center;
    padding-top: 25px;
    padding-bottom: 25px;
    background-color: rgba(240, 248, 255, 0.082);
}

.profile-card {
    width: 300px;
    height: 300px;
    background-color: rgb(129, 255, 255);
    border: 1px solid azure;
    margin: 5px;
    box-shadow: 1px 1px 5px gray;
    border-radius: 16px;
    padding: 15px;
    display: grid;
    place-items: center;
    color: black;
    text-shadow: none;
}

.profile-card button {
    width: 70%;
}

.profile-card img {
    width: 156px;
    background-color: white;
    padding: 25px;
    border-radius: 16px;
}

@media screen and (max-width: 1610px)
{
    .scroll-list {
        /* padding-bottom: 25px; */
        width: 100%;
        display: grid;
        grid-auto-flow: column;
        overflow-x: scroll;
    }
}

.message-card {
    width: 600px;
    display: inline-block;
}

@media screen and (max-width: 600px)
{
    .message-card {
        width: 100%;
    }
}

.image-card {
    width: 250px;
    height: 250px;
    border: 1px solid gray;
    border-radius: 100%;
    display: inline-block;
    margin: 50px;
    overflow: hidden;
    text-align: center;
    transition: 0.5s;
}

.image-card:hover {
    overflow: visible;
    border: none;
    opacity: 75%;
    transition: 0.5s;
}

.image-card img {
    width: 100%;
}

.game {
    padding: 15px;
    border-bottom: 1px solid gray;
    display:grid;
    grid-auto-flow: row;
}

.game-images {
    display: grid;
    grid-auto-flow: row;
    place-items: center;
    background: linear-gradient(to bottom, rgba(78, 78, 78, 0.7), rgba(0, 0, 0, 0.7));
    border: 1px solid white;
}

.game-images img {
    width: 456px;
    margin-top: 25px;
    margin-bottom: 25px;
    margin-left: 10px;
    margin-right: 10px;
    border-radius: 8px;
}

@media screen and (max-width: 456px)
{
    .game-images {
        padding: 10px;
    }

    .game-images img {
        width: 100%;
        margin-top: 0;
        margin-bottom: 10px;
    }
}

@media screen and (min-width: 545px) 
{
    .game {
        text-align: center;
    }
}

.game h1 {
    align-items: center;
    justify-content: center;
    margin: 0;
}

.link-button  {
    box-shadow: 1px 1px 5px black;
    border: 2px solid white;
    text-shadow: 1px 1px 3px black;
    margin-left: 15px;
    color: white;
    background-color: aqua;
    border-radius: 8px;
    font-size: medium;
    padding-left: 15px;
    padding-right: 15px;
    width: 150px;
    height: 25px;
    text-align: center;
    cursor: pointer;
}